export default {
	data() {
		return {
			form: {
				AUName: '',
				Phone: '',
				IdCard: '',
				Address: ''
			},
			rules: {
				AUName: {
					required: '',
					message: '请输入姓名'
				},
				Phone: {
					required: '',
					type: 'phone',
					message: '请输入手机号'
				},
				IdCard: {
					required: '',
					type: 'cardnum',
					message: '请输入身份证'
				}
			}
		};
	},
	methods: {
		submitForm: function(param) {
			uni.showModal({
				content: '表单数据内容：' + JSON.stringify(param),
				showCancel: false
			});
		}
	}
};
